import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms'

import { AppComponent } from './app.component';
import { MainNavComponent } from './main-nav/main-nav.component';
import { FollowersComponent } from './followers/followers.component';
import { PostsComponent } from './posts/posts.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { FollowerComponent } from './follower/follower.component';
import { PostsService } from './posts.service';
import { HomeComponent } from './section11/home/home.component';
import { AdminComponent } from './section11/admin/admin.component';
import { LoginComponent } from './section11/login/login.component';
import { NoAccessComponent } from './section11/no-access/no-access.component';
import { fakeBackendProvider, FakeLoginService } from './section11/fake.login.service';
import { AuthManager } from './common/auth.manager';
import { AuthGuard } from './auth-guard.service';
import { ChartsComponent } from './charts/charts.component';


@NgModule({
  declarations: [
    AppComponent,
    MainNavComponent,
    FollowersComponent,
    PostsComponent,
    NotFoundComponent,
    FollowerComponent,
    HomeComponent,
    AdminComponent,
    LoginComponent,
    NoAccessComponent,
    ChartsComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot([
      {path: '', component: FollowersComponent},
      {path: 'followers/:id', component: FollowerComponent},
      {path: 'followers', component: FollowersComponent},
      {path: 'posts', component: PostsComponent},
      {path: 'login', component: LoginComponent},
      {path: 'admin', component: AdminComponent, canActivate: [AuthGuard]},
      {path: '**', component: NotFoundComponent}
    ])
  ],
  providers: [  
    PostsService,
    { provide: HTTP_INTERCEPTORS, useClass: FakeLoginService, multi: true },
    fakeBackendProvider,
    AuthManager,
    AuthGuard
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
